import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

export default function AlertDialog({ open, alertType, close, confirm, customTitle = '', customMessage = '' }) {

  const createAlert = type => {
    let title, message
    if (typeof type === 'object') {
      title = 'Empresa j cadastrada'
      message = `A empresa ${type.razaoSocial} j est cadastrada no sistema com o CNPJ ${type.cnpj}.`
      return { title, message }
    }

    switch (type) {
      case 'plateNotFound':
        title = 'Placa no encontrada'
        message = 'A placa informada no corresponde a nenhum veculo da frota da viao selecionada. Para cadastrar um novo veculo, selecione a opo Veculos => "Cadastro de Veculo" no menu acima.'
        return { title, message }
      case 'invalidPlate':
        title = 'Placa invlida'
        message = 'Certifique-se de que a placa informada  uma placa vlida, com trs letras seguidas de 4 nmeros (ex: AAA-0000)'
        return { title, message }
      case 'plateExists':
        title = 'Placa j cadastrada!'
        message = 'A placa informada j est cadastrada. Para atualizar seguro, alterar dados ou solicitar baixa, utilize as opes acima. '
        return { title, message }
      case 'invalidModel':
        title = 'Modelo no cadastrado.'
        message = customMessage
        return { title, message }
      case 'inputError':
        title = 'Preenchimento invlido.'
        message = customMessage
        return { title, message }
      case 'fieldsMissing':
        title = 'Favor preencher todos os campos.'
        message = 'Os campos acima so de preenchimento obrigatrio. Certifique-se de ter preenchido todos eles.'
        return { title, message }
      case 'invalidInsurance':
        title = 'Favor verificar os dados do seguro.'
        message = 'As informaes referentes ao seguro do veculo so obrigatrias. Certifique-se de ter informado todas elas.'
        return { title, message }
      case 'filesNotFound':
        const subject = 'Procurao'
        title = 'Arquivos no encontrados'
        message = `No h nenhum arquivo anexado no sistema para a ${subject} selecionada. 
          Ao cadastrar ou atualizar a ${subject}, certifique-se de anexar o arquivo correspondente.`
        return { title, message }

      case 'empresaNotFound':
        title = 'Empresa no encontrada.'
        message = `A empresa informada no est registrada no sistema. Para cadastrar uma nova empresa, utilize a opo Empresas -> Cadastrar.`
        return { title, message }
      case 'seguradoraNotFound':
        title = 'Seguradora no encontrada.'
        message = `A seguradora informada no est registrada no sistema. Favor verificar o nome da seguradora.`
        return { title, message }

      case 'alreadyExists':
        title = 'CPF j cadastrado.'
        message = `O CPF informado corresponde a um scio j cadastrado. 
          Para remover ou editar as informaes dos scios, utilize a respectiva opo abaixo.`
        return { title, message }
      case 'numberNotValid':
        title = 'Nmero invlido.'
        message = 'O nmero digitado no  vlido.'
        return { title, message }
      case 'overShared':
        title = 'Participao societria invlida.'
        message = 'A participao societria informada excede a 100%.'
        return { title, message }
      case 'subShared':
        title = 'Participao societria invlida.'
        message = 'A participao societria informada no soma 100%. Favor informar todos os scios com suas respectivas participaes.'
        return { title, message }
      case 'veiculoPendente':
        title = 'Pendncias encontradas para este veculo.'
        message = 'Verifique se h alguma aprovao pendente quanto ao cadastro, seguro, transferncia ou alterao de dados do veculo.'
        return { title, message }
      case 'seguroVencido':
        title = 'Pendncias encontradas para o seguro deste veculo.'
        message = 'Para regularizar o seguro deste veculo, acesse a opo Veculos=>Seguros no menu acima.'
        return { title, message }
      case 'contratoVencido':
        title = 'Contrato expirado.'
        message = 'O contrato de concesso com o delegatrio selecionado est expirado.'
        return { title, message }
      default:
        return { title: null, message: null }
    }
  }
  const { title, message } = createAlert(alertType)

  if (title && message) return (
    <div>
      <Dialog
        open={open}
        onClose={close}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">{title}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            {message}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={close} color="primary">
            {confirm ? 'Cancelar' : 'Fechar'}
          </Button>
          {confirm && <Button onClick={close} color="primary" autoFocus>
            Confirmar
          </Button>}
        </DialogActions>
      </Dialog>
    </div>
  )
  else return null
}