import formatDate from '../Utils/formatDate'

export const delegatarioTable = [
    {
        field: 'razaoSocial',
        title: 'Razo Social',
    },
    {
        field: 'delegatarioStatus',
        title: 'Situao',
    },
    {
        field: 'vencimentoContrato',
        title: 'Data de Vencimento',
        filtering: false,
        render: rowData => formatDate(rowData.vencimentoContrato)
    },
    {
        field: 'cidade',
        title: 'Municpio',
    },
    {
        field: 'telefone',
        title: 'Telefone',
    },
    {
        field: 'frota',
        title: 'Tamanho da frota'
    }
  /*   {
        field: 'procuradoresList',
        title: 'Nome do Procurador',
        fSize: '0.3rem',
        render: rowData => rowData.procuradoresList
            ?
            rowData.procuradoresList.map(s => s.charAt(0).toUpperCase()
                .concat(s.slice(1).toLowerCase())).toString().replace(/,/g, ", ")
            :
            ''
    } */
]

export const sociosTable = [
    {
        field: 'nomeSocio',
        title: 'Nome',
    },
    {
        field: 'cpfSocio',
        title: 'CPF',
    },
    {
        field: 'razaoSocial',
        title: 'Delegatrio(s)',
    },
    {
        field: 'emailSocio',
        title: 'E-mail',        
        
    },
    {
        field: 'share',
        title: 'Participao Societria (%)',
        filtering: false,        
    }
]


export const procuradorTable = [
    {
        field: 'nomeProcurador',
        title: 'Nome',
    },
    {
        field: 'cpfProcurador',
        title: 'CPF',
    },
    {
        field: 'emailProcurador',
        title: 'E-mail',
    },
    {
        field: 'telProcurador',
        title: 'Telefone'        
    }
]

export const vehicleTable = [
    {
        field: 'placa',
        title: 'Placa',
    },
    {
        field: 'modeloCarroceria',
        title: 'Modelo da Carroceria',
    },
    {
        field: 'empresa',
        title: 'Delegatrio',
    },
    {
        field: 'seguradora',
        title: 'Seguradora',
    },
    {
        field: 'apolice',
        title: 'Nmero da Aplice',
    },
    {
        field: 'vencimento',
        title: 'Vencimento Seguro',
        render: rowData => formatDate(rowData.vencimento)
    },
    {
        field: 'indicadorIdade',
        title: 'Ano de Fabricao',
        filtering: false
    }
]

export const segurosTable = [
    {
        field: 'apolice',
        title: 'Nmero da Aplice',
    },
    {
        field: 'seguradora',
        title: 'Seguradora',
    },
    {
        field: 'empresa',
        title: 'Segurado',
    },
    {
        field: 'segurados',
        title: 'Nmero de veculos',
    },
    {
        field: 'vencimento',
        title: 'Vencimento',
        filtering: false,
        render: rowData => formatDate(rowData.vencimento)
    }
]

export const tables = [delegatarioTable].concat([sociosTable]).concat([procuradorTable]).concat([vehicleTable]).concat([segurosTable])
