import React from 'react'
import { Link } from 'react-router-dom'
import './home.css'

const featuredPosts = [
    {
        title: 'Veculos',
        date: '07/Nov',
        description:
            'Cadastre e altere os dados do veculo ou solicite sua baixa',
        link: '/veiculos',
        imageUrl: '/images/vehicles.jpg'
    },
    {
        title: 'Empresas',
        date: '07/Nov',
        description:
            'Cadastre e gerencie dados de empresas, scios e procuradores',
        link: '/empresas',
        imageUrl: '/images/empresas12.jpg'
    },
    {
        title: 'Consultas',
        date: '03/Dez',
        description:
            'Consulte e gerencie dados de veculos, seguros e empresas',
        link: '/consultas',
        imageUrl: '/images/consultas2.jpg'
    },
    {
        title: 'Relatrios',
        date: '03/Dez',
        description:
            'Acompanhe a situao dos veculos por meio de grficos e tabelas',
        link: '/relatorios',
        imageUrl: '/images/relatorios.jpg'
    },
]

export default function () {

    return (
        <React.Fragment>            
            <div
                style={{
                    minHeight: '83vh',
                    width: '100%',
                    height: 'auto',
                }}>
                <main className="homeMainPost">
                    <h1>
                        SisMob - MG
                    </h1>
                    <h2>
                        Sistema de Gesto da Mobilidade do Estado de Minas Gerais
                    </h2>
                </main>
                <section className='homeCardsContainer'>
                    {featuredPosts.map(({ link, title, description, imageUrl }, i) => (
                        <div key={i} className='homeCard'>

                            <Link to={link} style={{ textDecoration: 'none' }}>
                                <div className='homeCard'>
                                    <div className="homeCardText">
                                        <h6 className="homeCardText" style={{fontWeight: 500}}>
                                            {title}
                                        </h6>
                                        <p className="homeCardText">
                                            {description}
                                        </p>
                                       
                                    </div>
                                    <div className='homeImage'>
                                        <img src={imageUrl} alt={title} className='homeImage' />
                                    </div>
                                </div>
                            </Link>

                        </div>
                    ))}
                </section>
            </div >
        </React.Fragment >
    )
}